import React, { Component } from 'react';
import { Button, DatePicker, Icon, Select } from 'antd';
import '@/components/jfHeader/css/index.scss'
const { RangePicker } = DatePicker;
const { Option } = Select;
function onChange(date, dateString) {
    console.log(date, dateString);
}
function handleChange(value) {
    console.log(`selected ${value}`);
}
export default class head extends Component {
    static defaultProps = {
        time: true
    }
    static defaultProps = {
        open: true
    }
    static defaultProps = {
        text: ""
    }
    state = {
        flags: false,
    }
    render() {
        let { flag, list, time,open,text } = this.props
        let { flags } = this.state
        return (
            <div className="search-box">
                <div className="search">
                    <div className="boxs">
                        <Button size="small"></Button>
                        <div className="shs">
                            {
                                list.map(item => <i key={item.id}>{item.title}</i>)
                            }
                        </div>
                    </div>
                    <div className="time">
                        { time && <RangePicker onChange={onChange} allowClear="false" /> }
                        {!flag && <Button>{text}</Button>}
                    </div>

                  { open ? <div className="sea" >
                        {!flags ? <Button onClick={this.hold.bind(this, flags)}> <Icon type="down" /> </Button>
                            : <Button onClick={this.hold.bind(this, flags)}> <Icon type="up" /> </Button>}
                    </div> 
                    : <div className="sea" >
                        
                    </div>
                }
                </div>
                {flags && <div className="search-text">
                    <div className="left">
                        <div className="top">
                            <div className="item">
                                <label htmlFor=":">:</label>
                                <Select defaultValue="lucy" style={{ width: 120 }} onChange={handleChange}>
                                    <Option value="jack">Jack</Option>
                                    <Option value="lucy">Lucy</Option>
                                    <Option value="Yiminghe">yiminghe</Option>
                                </Select>
                            </div>
                            <div className="item">
                                <label htmlFor=":">:</label>
                                <Select defaultValue="lucy" style={{ width: 120 }} onChange={handleChange}>
                                    <Option value="jack">Jack</Option>
                                    <Option value="lucy">Lucy</Option>
                                    <Option value="Yiminghe">yiminghe</Option>
                                </Select>
                            </div>
                            <div className="item">
                                <label htmlFor=":">:</label>
                                <Select defaultValue="lucy" style={{ width: 120 }} onChange={handleChange}>
                                    <Option value="jack">Jack</Option>
                                    <Option value="lucy">Lucy</Option>
                                    <Option value="Yiminghe">yiminghe</Option>
                                </Select>
                            </div>
                        </div>
                        <div className="bottom">
                            <div className="item">
                                <label htmlFor=":">:</label>
                                <Select defaultValue="lucy" style={{ width: 120 }} onChange={handleChange}>
                                    <Option value="jack">Jack</Option>
                                    <Option value="lucy">Lucy</Option>
                                    <Option value="Yiminghe">yiminghe</Option>
                                </Select>
                            </div>
                            <div className="item">
                                <label htmlFor=":">:</label>
                                <RangePicker onChange={onChange} allowClear="true" />
                            </div>
                        </div>
                    </div>
                    <div className="right">
                        <Button size="small" type="primary"></Button> 
                        <Button size="small"></Button>  
                    </div>
                </div>}
            </div>

        );
    }
    hold(flags) {
        this.setState({ flags: !flags })
    }
}
